
'use client';

import { useState } from 'react';
import { useRouter } from 'next/navigation';
import { signInWithPopup, GoogleAuthProvider } from 'firebase/auth';
import { auth } from '@/lib/firebase';
import { useAuth } from '@/hooks/use-auth';
// import { useToast } from '@/hooks/use-toast';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Loader2, Eye, EyeOff } from 'lucide-react';
import Link from 'next/link';

export default function LoginPage() {
  const router = useRouter();
  const { signInAsGuest } = useAuth();
  // const { toast } = useToast();
  const [isGoogleLoading, setIsGoogleLoading] = useState(false);
  const [isGuestLoading, setIsGuestLoading] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  console.log('LoginPage rendered, signInAsGuest function:', signInAsGuest);

  const handleGoogleSignIn = async () => {
    setIsGoogleLoading(true);
    const provider = new GoogleAuthProvider();
    try {
      await signInWithPopup(auth, provider);
      // Store login status in localStorage
      if (typeof window !== 'undefined') {
        localStorage.setItem('userType', 'google');
        localStorage.setItem('loginStatus', 'google');
        localStorage.removeItem('guest');
        localStorage.removeItem('guestId');
      }
      // toast({
      //   title: 'Welcome!',
      //   description: 'Successfully signed in with Google.',
      // });
      router.push('/');
    } catch (error: any) {
      // toast({
      //   title: 'Google Sign-In Failed',
      //   description: error.message,
      //   variant: 'destructive',
      // });
      console.error('Google Sign-In Failed:', error.message);
    } finally {
      setIsGoogleLoading(false);
    }
  };

  const handleGuestSignIn = async () => {
    console.log('Guest sign-in button clicked!');
    setIsGuestLoading(true);
    try {
      // Generate a simple guest ID like guest_12345
      const randomNumber = Math.floor(Math.random() * 90000) + 10000; // 5-digit number
      const guestId = `guest_${randomNumber}`;
      
      console.log('Generated guest ID:', guestId);
      
      // Store guest session in localStorage with userType structure
      if (typeof window !== 'undefined') {
        localStorage.setItem('userType', 'guest');
        localStorage.setItem('guestId', guestId);
        localStorage.setItem('loginStatus', 'guest');
        localStorage.setItem('guest', '1');
        console.log('Stored guest data in localStorage');
      }
      
      // Use the existing guest sign-in function
      if (signInAsGuest) {
        console.log('Calling signInAsGuest function');
        signInAsGuest();
      } else {
        console.log('signInAsGuest function not available');
      }
      
      console.log(`Welcome, Guest! You are now browsing as a guest (${guestId}).`);
      console.log('Redirecting to main page...');
      
      router.push('/');
    } catch (error: any) {
      console.error('Guest Sign-In Failed:', error);
    } finally {
      setIsGuestLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-black text-white">
      {/* Navigation Header */}
      <nav className="flex items-center justify-between px-8 py-4 border-b border-gray-800">
        <div className="flex items-center space-x-8">
          <Link href="/" className="text-2xl font-bold bg-gradient-to-r from-purple-400 via-pink-500 to-red-500 bg-clip-text text-transparent">
            CINEMA
          </Link>
          <div className="hidden md:flex items-center space-x-6">
            <Link href="/" className="text-gray-300 hover:text-white transition-colors">Home</Link>
            <Link href="/about" className="text-gray-300 hover:text-white transition-colors">About</Link>
            <Link href="/blog" className="text-gray-300 hover:text-white transition-colors">Blog</Link>
            <Link href="/pages" className="text-gray-300 hover:text-white transition-colors">Pages</Link>
            <Link href="/contact" className="text-gray-300 hover:text-white transition-colors">Contact</Link>
          </div>
        </div>
        <div className="flex items-center space-x-4">
          <select className="bg-transparent text-gray-300 border-none outline-none">
            <option>English</option>
          </select>
          <Link href="/login" className="text-gray-300 hover:text-white transition-colors">Sign In</Link>
          <Link href="/signup" className="bg-white text-black px-4 py-2 rounded-lg hover:bg-gray-100 transition-colors">
            Register
          </Link>
        </div>
          </nav>

      {/* Main Content */}
      <div className="flex min-h-[calc(100vh-80px)]">
        {/* Left Panel - Visual */}
        <div className="hidden lg:flex lg:w-1/2 relative overflow-hidden">
          <div className="absolute inset-0 bg-gradient-to-br from-purple-900/20 via-pink-900/20 to-red-900/20"></div>
          <div className="relative z-10 flex items-center justify-center w-full">
            <div className="text-center">
              <div className="mb-8">
                <h1 className="text-8xl font-black bg-gradient-to-r from-purple-400 via-pink-500 to-red-500 bg-clip-text text-transparent mb-4">
                  CINEMA
                </h1>
                <p className="text-2xl text-gray-300 font-light">
                  Where Stories Come to Life
                </p>
              </div>
              {/* Futuristic Visual Element */}
              <div className="relative">
                <div className="w-64 h-64 mx-auto bg-gradient-to-br from-purple-500/20 to-pink-500/20 rounded-full blur-3xl"></div>
                <div className="absolute inset-0 w-64 h-64 mx-auto bg-gradient-to-tr from-blue-500/30 to-purple-500/30 rounded-full blur-2xl"></div>
                <div className="absolute inset-0 flex items-center justify-center">
                  <div className="w-32 h-32 bg-gradient-to-br from-purple-400 to-pink-500 rounded-full opacity-60"></div>
                </div>
              </div>
                </div>
              </div>
            </div>

        {/* Right Panel - Login Form */}
        <div className="w-full lg:w-1/2 flex items-center justify-center p-8">
          <div className="w-full max-w-md">
            {/* Welcome Message */}
            <div className="mb-8">
              <h2 className="text-4xl font-bold text-white mb-2">Hello !</h2>
              <h3 className="text-2xl font-semibold text-gray-300">Welcome Back</h3>
            </div>

            {/* Login Form */}
            <form className="space-y-6">
              {/* Email Input */}
              <div>
                <Input
                  type="email"
                  placeholder="Enter Email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  className="w-full h-12 bg-gray-900/50 border-gray-700 rounded-lg text-white placeholder-gray-400 focus:border-purple-500 focus:ring-purple-500/20"
                />
          </div>

              {/* Password Input */}
              <div className="relative">
                <Input
                  type={showPassword ? "text" : "password"}
                  placeholder="Enter Password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  className="w-full h-12 bg-gray-900/50 border-gray-700 rounded-lg text-white placeholder-gray-400 focus:border-purple-500 focus:ring-purple-500/20 pr-12"
                />
                <button
                  type="button"
                  onClick={() => setShowPassword(!showPassword)}
                  className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400 hover:text-white transition-colors"
                >
                  {showPassword ? <EyeOff className="h-5 w-5" /> : <Eye className="h-5 w-5" />}
                </button>
                </div>

              {/* Recover Password */}
              <div className="text-right">
                <Link href="/forgot-password" className="text-sm text-gray-400 hover:text-purple-400 transition-colors">
                  Recover Password ?
                </Link>
                </div>

              {/* Sign In Button */}
              <Button
                type="submit"
                className="w-full h-12 bg-white text-black font-semibold rounded-lg hover:bg-gray-100 transition-colors"
              >
                Sign In
              </Button>
            </form>

            {/* Divider */}
            <div className="relative my-8">
              <div className="absolute inset-0 flex items-center">
                <div className="w-full border-t border-gray-700"></div>
                </div>
              <div className="relative flex justify-center text-sm">
                <span className="px-4 bg-black text-gray-400">Or continue with</span>
                </div>
            </div>

            {/* Social Login Buttons */}
            <div className="grid grid-cols-3 gap-4 mb-8">
              {/* Google */}
              <Button
                onClick={handleGoogleSignIn}
                disabled={isGoogleLoading || isGuestLoading}
                className="h-12 bg-gray-900/50 border border-gray-700 rounded-lg hover:bg-gray-800 transition-colors flex items-center justify-center"
              >
                {isGoogleLoading ? (
                  <Loader2 className="h-5 w-5 animate-spin" />
                ) : (
                  <svg className="h-6 w-6" viewBox="0 0 24 24">
                    <path
                      fill="currentColor"
                      d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z"
                    />
                    <path
                      fill="currentColor"
                      d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z"
                    />
                    <path
                      fill="currentColor"
                      d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l2.85-2.22.81-.62z"
                    />
                    <path
                      fill="currentColor"
                      d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z"
                    />
                  </svg>
                )}
              </Button>

              {/* Apple */}
              <Button
                className="h-12 bg-gray-900/50 border border-gray-700 rounded-lg hover:bg-gray-800 transition-colors flex items-center justify-center"
              >
                <svg className="h-6 w-6" viewBox="0 0 24 24" fill="currentColor">
                  <path d="M18.71 19.5c-.83 1.24-1.71 2.45-3.05 2.47-1.34.03-1.77-.79-3.29-.79-1.53 0-2 .77-3.27.82-1.31.05-2.3-1.32-3.14-2.53C4.25 17 2.94 12.45 4.7 9.39c.87-1.52 2.43-2.48 4.12-2.51 1.28-.02 2.5.87 3.29.87.78 0 2.26-1.07 3.81-.91.65.03 2.47.26 3.64 1.98-.09.06-2.17 1.28-2.15 3.81.03 3.02 2.65 4.03 2.68 4.04-.03.07-.42 1.44-1.38 2.83M13 3.5c.73-.83 1.94-1.46 2.94-1.5.13 1.17-.34 2.35-1.04 3.19-.69.85-1.83 1.51-2.95 1.42-.15-1.15.41-2.35 1.05-3.11z"/>
                </svg>
              </Button>

              {/* Facebook */}
              <Button
                className="h-12 bg-gray-900/50 border border-gray-700 rounded-lg hover:bg-gray-800 transition-colors flex items-center justify-center"
              >
                <svg className="h-6 w-6" viewBox="0 0 24 24" fill="currentColor">
                  <path d="M24 12.073c0-6.627-5.373-12-12-12s-12 5.373-12 12c0 5.99 4.388 10.954 10.125 11.854v-8.385H7.078v-3.47h3.047V9.43c0-3.007 1.792-4.669 4.533-4.669 1.312 0 2.686.235 2.686.235v2.953H15.83c-1.491 0-1.956.925-1.956 1.874v2.25h3.328l-.532 3.47h-2.796v8.385C19.612 23.027 24 18.062 24 12.073z"/>
                </svg>
              </Button>
            </div>

            {/* Guest Login */}
            <Button
              onClick={handleGuestSignIn}
              disabled={isGoogleLoading || isGuestLoading}
              className="w-full h-12 bg-transparent border border-gray-700 text-white rounded-lg hover:bg-gray-900/50 transition-colors flex items-center justify-center gap-2"
            >
              {isGuestLoading ? (
                <Loader2 className="h-5 w-5 animate-spin" />
              ) : (
                <svg className="h-5 w-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z" />
                </svg>
              )}
              Continue as Guest
            </Button>

            {/* Registration Link */}
            <div className="text-center mt-8">
              <p className="text-gray-400">
                Don't have an account ?{' '}
                <Link href="/signup" className="text-purple-400 hover:text-purple-300 transition-colors font-semibold">
                  Create Account!
                </Link>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
